import { Component, Host, h, Prop, Event, EventEmitter, State } from '@stencil/core';
import { IndividualFormChange, NewIndividual, ObjectProperty } from '../../global/global';
import { removePrefix } from '../../utils/utils';
import { NewIndividualSubFormCustomEvent } from '../../components';

@Component({
    tag: 'new-individual-sub-form',
    styleUrl: 'new-individual-sub-form.scss',
    shadow: true,
})
export class NewIndividualSubForm {
    @Prop() objectProperty: ObjectProperty;
    @Prop() owlPrefix: string;

    @Prop() componentId: string = '';

    @Prop() level: number = 1;

    @State() maxLevel: number = 4;

    @Event({ composed: true })
    individualSubFormChange: EventEmitter<IndividualFormChange>;
    @Event({ composed: false })
    individualInnerChange: EventEmitter<IndividualFormChange>;

    onInnerChange(method: string, event?: Event, prop?: string, index?: number, id?: string) {
        const idNew = id ? this.componentId + '-' + id : this.componentId;

        if (this.level === 1) {
            this.individualSubFormChange.emit({ method, event, prop, index, id: idNew });
        } else {
            this.individualInnerChange.emit({ method, event, prop, index, id: idNew });
        }
    }

    formatStringByNamespace(str: string) {
        return removePrefix(this.owlPrefix, str);
    }

    render() {
        return (
            <Host>
                <div>
                    <div class='row d-flex align-items-center'>
                        <div class='col-1'>
                            <label class='form-label mt-1' htmlFor='name'>
                                Názov:
                            </label>
                        </div>
                        <div class='col-12 col-sm-4'>
                            <input
                                class={`form-control litePurpleColor ${
                                    !(this.objectProperty.individual as NewIndividual).name
                                        ? 'highlight'
                                        : ''
                                }`}
                                id='name'
                                name='name'
                                type='text'
                                placeholder='Zadaj názov nového individuála'
                                value={this.formatStringByNamespace(
                                    (this.objectProperty.individual as NewIndividual).name,
                                )}
                                onInput={(event) =>
                                    this.onInnerChange('handleInputChange', event, 'name')
                                }
                            />
                        </div>
                    </div>
                    <div class='container-fluid py-3'></div>
                    <div class='border-solid ml-auto bg-color-transparent pb-3 pt-2 px-1 px-sm-4'>
                        <div class='col d-flex justify-content-start text-info mt-1 mb-2 ms-2 text-truncate'>
                            Objektové vlastnosti{(this.objectProperty.individual as NewIndividual).name ? ':' : ''} &nbsp;<strong>{this.formatStringByNamespace((this.objectProperty.individual as NewIndividual).name)}</strong>
                        </div>
                        {(this.objectProperty.individual as NewIndividual).objectProperties &&
                            (this.objectProperty.individual as NewIndividual).objectProperties.map(
                                (objectProperty, index) => (
                                    <div class='border-solid ml-auto bg-color-instance-transparent my-4 pt-4 px-1 px-sm-4'>
                                        <div>
                                          <div class="row">
                                            <div class="col-12 d-flex justify-content-start mb-3 mt-0 text-truncate">Objektová
                                              vlastnosť{(this.objectProperty.individual as NewIndividual).name ? ':' : ''} &nbsp;
                                              <strong>{this.formatStringByNamespace((this.objectProperty.individual as NewIndividual).name)}</strong>
                                            </div>
                                          </div>
                                            <select
                                                id='name'
                                                name='name'
                                                class={`form-select litePurpleColor ${
                                                    !objectProperty.name ? 'highlight' : ''
                                                }`}
                                                onChange={(event) =>
                                                    this.onInnerChange(
                                                        'handleObjectPropertyChange',
                                                        event,
                                                        'name',
                                                        index,
                                                    )
                                                }
                                            >
                                                <option value={''} selected>
                                                    Vyber objektovú vlastnosť...
                                                </option>
                                                {(
                                                    this.objectProperty.individual as NewIndividual
                                                ).objectPropertyOptions.map((option) => (
                                                    <option
                                                        value={option}
                                                        selected={option === objectProperty.name}
                                                    >
                                                        {this.formatStringByNamespace(option)}
                                                    </option>
                                                ))}
                                            </select>
                                            {objectProperty.name && (
                                                <select
                                                    id='selectedClass'
                                                    name='selectedClass'
                                                    class={`form-select litePurpleColor mt-2 ${
                                                        !objectProperty.selectedClass
                                                            ? 'highlight'
                                                            : ''
                                                    }`}
                                                    onChange={(event) =>
                                                        this.onInnerChange(
                                                            'handleObjectPropertyChange',
                                                            event,
                                                            'selectedClass',
                                                            index,
                                                        )
                                                    }
                                                >
                                                    <option value={''} selected>
                                                        Vyber triedu individuála...
                                                    </option>
                                                    {objectProperty.classOptions.map((option) => (
                                                        <option
                                                            value={option}
                                                            selected={
                                                                option ===
                                                                this.objectProperty.selectedClass
                                                            }
                                                        >
                                                            {this.formatStringByNamespace(option)}
                                                        </option>
                                                    ))}
                                                </select>
                                            )}
                                            {this.level < this.maxLevel &&
                                                objectProperty.selectedClass &&
                                                objectProperty.name !==
                                                    this.owlPrefix + 'usesTechnique' && (
                                                    <div class='row align-items-center my-2'>
                                                        <label
                                                            class='col-7 d-flex justify-content-end'
                                                            htmlFor='isExistingIndividual'
                                                        >
                                                            Je existujúci individuál ?
                                                        </label>
                                                        <div class='col-5 checkbox-wrapper-2'>
                                                            <input
                                                                type='checkbox'
                                                                name='isExistingIndividual'
                                                                class={`litePurpleColor sc-gJwTLC ikxBAC col-6 d-flex justify-content-start`}
                                                                checked={
                                                                    objectProperty.isExistingIndividual
                                                                }
                                                                onChange={(event) =>
                                                                    this.onInnerChange(
                                                                        'handleObjectPropertyChange',
                                                                        event,
                                                                        'isExistingIndividual',
                                                                        index,
                                                                    )
                                                                }
                                                            />
                                                        </div>
                                                    </div>
                                                )}
                                            {objectProperty.selectedClass &&
                                                objectProperty.individualOptions &&
                                                objectProperty.isExistingIndividual && (
                                                    <select
                                                        id='individualOptions'
                                                        name='individualOptions'
                                                        class={`form-select litePurpleColor ${
                                                            !objectProperty.individual
                                                                ? 'highlight'
                                                                : ''
                                                        }`}
                                                        onChange={(event) =>
                                                            this.onInnerChange(
                                                                'handleObjectPropertyChange',
                                                                event,
                                                                'individual',
                                                                index,
                                                            )
                                                        }
                                                    >
                                                        <option value={''} selected>
                                                            Vyber individuála...
                                                        </option>
                                                        {objectProperty.individualOptions.map(
                                                            (option) => (
                                                                <option
                                                                    value={option}
                                                                    selected={
                                                                        option ===
                                                                        objectProperty.individual
                                                                    }
                                                                >
                                                                    {this.formatStringByNamespace(
                                                                        option,
                                                                    )}
                                                                </option>
                                                            ),
                                                        )}
                                                    </select>
                                                )}
                                            {objectProperty.selectedClass &&
                                                objectProperty.individual &&
                                                !objectProperty.isExistingIndividual && (
                                                    <new-individual-sub-form
                                                        componentId={index.toString()}
                                                        level={this.level + 1}
                                                        objectProperty={objectProperty}
                                                        owlPrefix={this.owlPrefix}
                                                        onIndividualInnerChange={(
                                                            change: NewIndividualSubFormCustomEvent<IndividualFormChange>,
                                                        ) =>
                                                            this.onInnerChange(
                                                                change.detail.method,
                                                                change.detail.event,
                                                                change.detail.prop,
                                                                change.detail.index,
                                                                change.detail.id,
                                                            )
                                                        }
                                                    />
                                                )}
                                            <button
                                                type='button'
                                                class='btn btn-outline-danger trash btn-sm btn-circle my-2'
                                                data-bs-toggle='tooltip'
                                                data-bs-placement='bottom'
                                                title='Vymaž objektovú vlastnosť'
                                                onClick={() =>
                                                    this.onInnerChange(
                                                        'removeObjectProperty',
                                                        null,
                                                        null,
                                                        index,
                                                    )
                                                }
                                            ></button>
                                        </div>
                                    </div>
                                ),
                            )}
                        <div>
                            <button
                                type='button'
                                class='btn btn-outline-success btn-sm btn-circle plus mb-4'
                                data-bs-toggle='tooltip'
                                data-bs-placement='bottom'
                                title='Pridaj novú objektovú vlastnosť'
                                onClick={() => this.onInnerChange('addObjectProperty')}
                            ></button>
                        </div>
                    </div>
                    <div class='container-fluid py-3'></div>
                    <div class='border-solid ml-auto bg-data-color-transparent pb-3 pt-2 px-1 px-sm-4'>
                        <div class='col d-flex justify-content-start text-info my-1 ms-2 text-truncate'>
                            Dátové vlastnosti{(this.objectProperty.individual as NewIndividual).name ? ':' : ''} &nbsp;<strong>{this.formatStringByNamespace((this.objectProperty.individual as NewIndividual).name)}</strong>
                        </div>
                        <div>
                            {(this.objectProperty.individual as NewIndividual).dataProperties &&
                                (
                                    this.objectProperty.individual as NewIndividual
                                ).dataProperties.map((dataProperty, indexDataProperty) => (
                                    <div class='row px-2 pb-2'>
                                        <div class='col-6'>
                                            <select
                                                id='name'
                                                name='name'
                                                class={`form-select litePurpleColor ${
                                                    !dataProperty.name ? 'highlight' : ''
                                                }`}
                                                onInput={(event) =>
                                                    this.onInnerChange(
                                                        'handleDataPropertyChange',
                                                        event,
                                                        'name',
                                                        indexDataProperty,
                                                    )
                                                }
                                            >
                                                <option value={''} selected>
                                                    Vyber dátovú vlastnosť...
                                                </option>
                                                {dataProperty.dataPropertyOptions.map((option) => (
                                                    <option
                                                        value={JSON.stringify(option)}
                                                        selected={option.name === dataProperty.name}
                                                    >
                                                        {this.formatStringByNamespace(option.name)}
                                                    </option>
                                                ))}
                                            </select>
                                        </div>
                                        <div class='col-5'>
                                            <input
                                                type='text'
                                                class={`form-control litePurpleColor ${
                                                    !dataProperty.value ? 'highlight' : ''
                                                }`}
                                                value={dataProperty.value}
                                                onInput={(event) =>
                                                    this.onInnerChange(
                                                        'handleDataPropertyChange',
                                                        event,
                                                        'value',
                                                        indexDataProperty,
                                                    )
                                                }
                                                placeholder={'Zadaj hodnotu...'}
                                            />
                                        </div>
                                        <div class='col-1'>
                                            <button
                                                type='button'
                                                class='trash btn btn-outline-danger btn-sm btn-circle mb-2'
                                                data-bs-toggle='tooltip'
                                                data-bs-placement='bottom'
                                                title='Vymaž dátovú vlastnosť'
                                                onClick={() =>
                                                    this.onInnerChange(
                                                        'removeDataProperty',
                                                        null,
                                                        null,
                                                        indexDataProperty,
                                                    )
                                                }
                                            ></button>
                                        </div>
                                    </div>
                                ))}
                            <button
                                type='button'
                                class='plus btn btn-outline-success btn-sm btn-circle mb-4 '
                                data-bs-toggle='tooltip'
                                data-bs-placement='bottom'
                                title='Pridaj novú dátovú vlastnosť'
                                onClick={() => this.onInnerChange('addDataProperty')}
                            ></button>
                        </div>
                    </div>
                </div>
            </Host>
        );
    }
}
